body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  background: url('https://xpert.link/uploads/2020/08/6912908e25234edbfe2659ec634e5e0f.jpeg') center center no-repeat;
  background-size: cover;
  color: white;
  height: 100%;
  width: 100%;
}

.content {
  margin: auto;
  margin-top: 20px;
  width: 100%;
  height: auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
}

.title {
  padding-top: 50px;
  text-align: center;
  font-weight: 600;
  font-size: 3em;
  letter-spacing: 0.15rem;
  color: #fff;
}

label {
  color: #333;
}

.label-head {
  font-weight: 600;
  font-size: 20px;
}

.result-container {
  text-align: center;
  font-weight: 600;
  font-size: 4em;
  letter-spacing: 0.15rem;
  padding: 5px 0 10px;
}

#result {
  color: #000;
}

.col25.form-group.col {
  -webkit-flex: 0 0 20%;
          flex: 0 0 20%;
  max-width: 20%;
}

